.factoryForm{z-index: 10000;
    position: fixed;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex; flex-direction: column;
    align-items: center; width: 95%;
    margin: 0 auto;
    .factoryInput__container{display: flex;
        justify-content: space-between;
        align-items: center; flex-wrap: wrap;
        position: relative; margin-bottom: 20px;
        width: 100%;
        .factoryInput__input{  
            flex:auto; height: 40px;
            border: 1px solid #1D4E8F;
            padding: 0px 20px; color: #444444; padding-left: 50px;
            border-radius: 10px; font-weight: 500; font-size: 12px;
            background-color: #F5F1E6;}
        .factoryInput__input::placeholder{color: #444444;}
        .factoryInput__arrow{position: absolute; right: 0; 
            width: 40px; height: 40px; border-radius: 10px;
            background-color: #1D4E8F; padding: 10px 0px;
            text-align: center; color: #fff;
            cursor: pointer;}
    }
    .factoryInput__lable{
        height: 35px; width: 35px;
        color: #1D4E8F; cursor: pointer;
        position: absolute; left: 10px; top: 15%;
        font-size: 16px;
        text-align: center;
    }
    .factoryForm__attachment{display: flex; flex-direction: column;
        justify-content: center;
        img{width: 80px; height: 80px; border-radius: 40px;}
        .factoryForm__clear{color: #04aaff; cursor: pointer;
            text-align: center;
            span{margin-right: 10px; font-size: 12px;}
        }
    }
}